import React, { useState, useContext, useEffect } from 'react'
import { Spin, Typography, Input, Table, Tag, Button, message, notification, Icon } from 'antd'
import MonitorContext from '../../../../../../context/monitor/monitorContext'
import { Link, NavLink, } from 'react-router-dom'
import { InfoCircleOutlined } from '@ant-design/icons';
import moment from 'moment'

import '../Trigger/Trigger.scss'



const Trigger = (props) => {
    //页面挂载成功，useContext拉取远端数据
    const monitorContext = useContext(MonitorContext)
    const {changeFlag, clearDoneList, flag=false,clearId, timeData = '', alarm = '', loading, currentId, doneList = [], doneListNum = 0, getDoneList } = monitorContext
    let _id; //存放当前请求的主机id，首选从redux里拿
    if (currentId) {
        _id = Number(currentId)
    } else {
        _id = props.location.query ? Number(props.location.query.id) : ''
    }

    useEffect(() => {  //useEffect 重构生命周期didMount
        _id && getDoneList(_id)
        return handleComponentWillUnmount
        // eslint-disable-next-line
    }, [])

    const handleComponentWillUnmount = () => {
        // clearId()
        clearDoneList()
    }
    const close = () => {
        console.log(
            'Notification was closed. Either the close button was clicked or duration time elapsed.',
        );
    };
    const openNotification_empty = () => {
        const key = `open${Date.now()}`;
        const btn = (
            <Button type="primary" size="small" onClick={() => notification.close(key)}>
                确定
          </Button>
        );
        notification.open({
            message: '新增报警事件',
            description:
                '当前暂无新增报警事件',
            btn,
            key,
            onClose: close,
        });
    };
    const openNotification = () => {
        const key = `open${Date.now()}`;
        const btn = (
            <Button type="primary" size="small" onClick={() => notification.close(key)}>
                确定
          </Button>
        );
        notification.open({
            message: '新增报警事件',
            description:
                `当前新增报警事件信息如下所示:
                 事件类型:${alarm.event_type}，级别:${alarm.trigger},内容:${alarm.log}`,
            btn,
            key,
            onClose: close,
        });
    };
    const handleAlarmClick = () => {
        if (timeData && (timeData / 1000) < 300) {//五分钟内发生的触发事件会进行报警。
            openNotification()
            //报警时间和当前时间的时间戳差值小于30秒，显示最新报警事件的弹窗提示。
        }
        else{
            openNotification_empty()
        }
    }
    if (alarm) { console.log(alarm)
    if(flag===false && doneList.length>0){
        console.log(flag,doneList,'ttt!')
            changeFlag()
        console.log(flag,doneList,'ttt!')
            handleAlarmClick()
        }

    }
  

  

  

  
    // console.log('alarm',moment(alarm.date).valueOf())
    // console.log(Number(1590554991748-1588840489993)/1000/3600/24)

    const { Text } = Typography
    const { Search } = Input

    //分页
    const [currentPage, setCurrentPage] = useState(1)
    const paginationProps = {
        showSizeChanger: false,
        showQuickJumper: false,
        showTotal: () => `共${doneListNum}条`,
        pageSize: 4,
        current: currentPage,
        total: doneListNum,
        onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
        onChange: (current) => changePage(current),
    }

    const changePageSize = (a, b) => {
        console.log(a, b)
    }

    const changePage = (v) => {
        console.log(v)
        setCurrentPage(v)
    }




    const columns = [
        {
            title: '事件类型',
            dataIndex: 'type',
            key: 'type',
        },
        {
            title: '级别',
            dataIndex: 'level',
            key: 'level',
            render: text => <span style={{ width: '100%', backgroundColor: 'FFFF00' }}>{text}</span>,

        },
        {
            title: '内容',
            dataIndex: 'info',
            key: 'info',
        },
        {
            title: '日期',
            dataIndex: 'date',
            key: 'date',
        },

    ];

    const data = []; // 初始化antd表格

    if (doneList.length > 0) {
        doneList.forEach((item, index) => {
            data.push({
                key: String(index + 1),
                type: item.event_type ? item.event_type : '暂无',
                level: item.trigger ? item.trigger : '暂无',
                info: item.log ? item.log : '暂无',
                date: item.date ? moment(item.date).format('YYYY-MM-DD HH:mm:ss') : '暂无',

            })

        })

    }

   

    // const debounce=(func, wait=500) =>{
    //     let timeout;  // 定时器变量
    //     return function(event){
    //         clearTimeout(timeout);  // 每次触发时先清除上一次的定时器,然后重新计时
    //         event.persist && event.persist()   //保留对事件的引用
    //         //const event = e && {...e}   //深拷贝事件对象
    //         timeout = setTimeout(()=>{
    //             func(event)
    //         }, wait);  // 指定 xx ms 后触发真正想进行的操作 handler
    //     };
    // }

    //    debounce( handleAlarmClick);

   
   
   

   
 


   

    if (loading) {
        return <Spin className="myLoading" tip="加载中..." size='large'>
            <div className='Trigger'>
                <div className='Trigger-table' style={{ marginBottom: '10px' }}>
                    <Table columns={columns} dataSource={data} pagination={paginationProps} />
                </div>
            </div>
        </Spin>
    }
    return (
        <div className='Trigger'>
            <div className='Trigger-table' style={{ marginBottom: '10px', height: '100%' }}>
                {/* <h6 onClick={handleAlarmClick} className='alarm-tab'><InfoCircleOutlined />&nbsp;查看新增的报警事件</h6> */}
                <Table columns={columns} dataSource={data} pagination={paginationProps} />
            </div>
        </div>
    )
}

export default React.memo(Trigger)




